.section-partners {

    padding-top: 60px;
    padding-bottom: 60px;
    background: linear-gradient(94.46deg, #36EAC2 3.97%, #31E9C5 25.79%, #07E0DE 51.58%, #46EDB9 76.54%);
}

.partners {
    position: relative;
    z-index: 10;
}

.partners__h2 {
    max-width: 100%;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 35px;
    line-height: 123.5%;
    /* or 43px */

    text-align: center;
    text-transform: uppercase;

    color: #FFFFFF;
}


// .patners__main-row {
//     overflow-x: auto;
// }

.partners__row {
    margin-top: 30px;


    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 15px;

    div {
        flex-shrink: 0;

    }


    div:not(:first-child) {
        margin-left: 10px;
    }

    &_1 {
        margin-top: 60px;
    }
}


.partners__item:last-child {
    margin-right: 20px;
}

.partners__item:first-child {
    margin-left: 20px;
}

.partners__item img {
    transition: all .3s;
    padding: 10px;
    border-radius: 10px;
}







@media(max-width:768px) {
    .section-partners {
        padding-top: 60px;
        padding-bottom: 60px;
    }

    .partners__row {
        &_1 {
            margin-top: 40px;
        }
    }

    .partners__item:last-child {
        margin-right: 0px;
    }

    .partners__item:first-child {
        margin-left: 0px;
    }

}