.section-infrastructure {
    padding: 50px 0;
    position: relative;

    &_page-infrastruture {
        padding: 120px 0 200px;
    }
}

.infrastructure__body {
    position: relative;
    z-index: 10;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.infrastructure__item {
    flex-basis: calc(33.333% - 20px);
    margin-top: 80px;

}


.infrastructure__item_m0 {
    margin: 0;

}
//support

.infrastructure_support {
    max-width: 1000px;
}

.infrastructure__item_fb40 {
    flex-basis: 50%;
}

.infrastructure__title {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
    line-height: 147%;
    /* or 25px */

    text-transform: uppercase;

    color: #00516B;
}

.infrastructure__list {
    margin-top: 20px;
    list-style: none;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 100;
    font-size: 17px;
    line-height: 113.5%;
    color: #00516B;

    li {
        display: flex;
        align-items: center;
    }

    li:not(:first-child) {
        margin-top: 10px;
    }

}

.infrastructure__list-wrap {
    display: flex;

    ul:last-child {
        margin-left: 30px;
    }
}

@media(max-width:768px) {
    .infrastructure__item {
        flex-basis: calc(50% - 20px);
    }
}

@media(max-width:550px) {
    .infrastructure__item {
        flex-basis: 100%;
    }

    .infrastructure__item {
        margin-top: 30px;
    }

    .infrastructure__list {
        margin-top: 10px;
        font-size: 14px;
    }
}

.infrastructure__icon {
    display: block;
    width: 6px;
    height: 6px;
    margin-right: 15px;
    background-color: #00516B;
    transform: rotate(45deg);
    flex-shrink: 0;
}