.about-group {
    position: relative;
    z-index: 10;
}



.section-about-group {
    position: relative;
    padding: 100px 0;
    opacity: 0;

}

.about-group__body {
    position: relative;
    width: 640px;
    height: 640px;
    margin: 0 auto;
    background-image: url(../img/svg/bg/about-group.svg);
    background-size: contain;
    background-position: 50% 50%;
    background-repeat: no-repeat;
}

.about-group__title {
    position: absolute;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #3BBEAA;
    font-family: "Montserrat", sans-serif;
    font-size: 1.6rem;
}

.about-group__item {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    text-decoration: none;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 2px solid #3BBEAA;
    color: #3BBEAA;
    font-weight: 600;
    font-size: 1.2rem;
    transition: all .3s;




    &_1 {
        top: -5%;
        left: 50%;
        transform: translateX(-50%);
    }

    &_2 {
        top: 15%;
        left: 5%;
    }

    &_3 {
        top: 15%;
        right: 5%;
    }

    &_4 {
        bottom: 5%;
        left: 12.5%;
    }

    &_5 {
        bottom: 5%;
        right: 12.5%;
    }
}

@media(max-width:768px) {
    .about-group__body {
        width: 100%;
        height: 500px;
    }

    .about-group__item {
        width: 120px;
        height: 120px;
    }
}

@media(max-width:550px) {
    .about-group__body {
        width: 100%;
        height: 350px;
    }

    .about-group__item {
        width: 95px;
        height: 95px;
        font-size: 14px;
    }

    .about-group__title {
        font-size: 1.2rem;
    }
}





.card-about-group {
    margin-top: 40px;
}

.card-about-group__body {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
}

.card-about-group__item {
    flex-basis: calc(33.333% - 20px);
    margin: 10px;
    background-color: #3BBEAA;

}


.card-group {
    color: #fff;


}


.card-group__header {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
    padding: 30px 60px 30px 30px;
    transition: all .3s;

    &:hover {
        .card-group__arrow-wrap {
            background-color: #2FEECB;
            opacity: 1 !important;


        }
    }

}


.card-group__arrow-wrap {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 50px;
    height: 100%;
    background-color: #2FEECB;
    opacity: 0;
    transition: all .3s;

    svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(180deg);
        width: 20px;
        height: 100%;
        transition: all .15s;
    }
}

.card-about-group__h3 {
    margin-left: 20px;
    font-size: 1.4rem;
    font-family: "Montserrat", sans-serif;
}

.card-group__text {
    position: relative;
    padding: 30px 60px 30px 30px;
    display: none;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        width: 50px;
        height: 100%;
        background-color: #2FEECB;
    }
}

@media(max-width:940px) {
    .card-about-group__body {
        flex-direction: column;
        align-items: stretch;
    }

    .card-about-group__item {
        margin: 0;
    }

    .card-about-group__item:not(:first-child) {
        margin-top: 10px;
    }

    .card-about-group__icon {
        img {
            width: 60px;
        }
    }
}

@media(max-width:550px) {
    .section-about-group {
        padding: 40px 0;
    }
}