:root {
    --animate-duration: 1.75s;
    --animate-delay: 1.3s;

}

*,
::before,
::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    font-family: $titleFont;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}

img {
    max-width: 100%;
}

html,
body {
    font-family: $textFont;
    font-size: 16px;
    color: $sec-color;
    line-height: 1.5;
    overflow-x: hidden;
    background-color: $sec-color;
}

body {
    cursor: url('../img/cursor.png'), auto !important;
    cursor: url('../img/cursor.png'), n-resize !important;
    cursor: url('../img/cursor.png'), ne-resize !important;
    cursor: url('../img/cursor.png'), e-resize !important;
    cursor: url('../img/cursor.png'), se-resize !important;
    cursor: url('../img/cursor.png'), s-resize !important;
}

@media(max-width: 768px) {

    body {
        font-size: 14px;
    }
}

@media(max-width: 425px) {

    body {
        font-size: 12px;
    }
}

.container-fluid {
    max-width: 1980px;
    margin: 0 auto;
    background-color: #fff;
}

.container {
    max-width: 1170px;
    padding: 0 15px;
    margin: 0 auto;
}


@media(max-width:600px) {
    .fancybox-content {
        padding: 14px;
    }
}

.h2-short-line {
    display: inline-block;
    position: relative;
    z-index: 10;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 35px;
    line-height: 123.5%;
    text-transform: uppercase;
    color: #5CEFC1;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        width: 220px;
        height: 1px;
        background-color: #00516B;
        bottom: -5px;
        transform: translateX(-60px);
    }
}

@media(max-width:550px) {
    .h2-short-line {
        font-size: 26px;
    }
}