.soft-dev__body {
    display: flex;
    align-items: center;
}

.soft-dev__item {
    flex-basis: 50%;
    position: relative;
    z-index: 10;
}

.soft-dev__item:last-child {
    img {
        display: block;
        margin: 0 auto;
    }
}

.soft-dev__p:first-child {
    margin-top: 30px;
}

.soft-dev__p:not(:first-child) {
    margin-top: 20px;
}

@media(max-width:650px) {
    .soft-dev__body {
        flex-direction: column;
    }

    .soft-dev__item:last-child {
        margin-top: 40px;
        max-width: 80%;
    }
}

.section-devops-table {
    position: relative;
    z-index: 10;
    padding: 0 0 50px;



    img {
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
}

.section-support {
    padding: 50px 0;
}

.section-devops-frontend {
    padding: 50px 0;
}

.section-modern-dev {
    padding: 50px 0;
}


.modern-dev__body {
    display: flex;
    align-items: center;
}

.modern-dev__item {
    flex-basis: 50%;

    img {
        display: block;
        margin: 0 auto;
    }
}

@media(max-width:650px) {
    .section-support {
        padding: 25px 0;
    }

    .section-devops-frontend {
        padding: 25px 0;
    }

    .section-modern-dev {
        padding: 0 0 25px;
    }

    .section-modern-dev {
        padding-top: 0;
    }

    .modern-dev__item {
        flex-basis: 100%;

    }

    .modern-dev__item:first-child {
        display: none;
    }
}

.process-consists__body {
    display: flex;
    align-items: center;
}

.process-consists__item {
    flex-basis: 50%;
}

.process-consists__item:last-child {
    margin-left: 100px;
}

.process-consists__border-text {
    border: 2px solid #70F1C9;
    padding: 50px 25px;
    text-align: center;
}

.infrastructure__body_process-consists {
    flex-wrap: nowrap;
}

.infrastructure__item_process-consists {
    margin-top: 40px
}

.infrastructure__item_process-consists:last-child {
    margin-left: 40px;
}

.infrastructure__title_process-consists {
    margin-top: 60px;
    max-width: 550px;
}

.process-consists__footer {
    padding-bottom: 80px;
}

@media(max-width:768px) {
    .process-consists__item:last-child {
        margin-left: 50px;
    }
}

@media(max-width:650px) {
    .process-consists__body {
        flex-direction: column;

        .infrastructure__item {
            margin-top: 0;
        }
    }

    .process-consists__item:last-child {
        margin-top: 40px;
        margin-left: 0;
    }

    .infrastructure__body_process-consists {
        flex-direction: column;
    }

    .infrastructure__item_process-consists {
        margin-top: 30px;
    }

    .infrastructure__item_process-consists:last-child {
        margin-left: 0;
    }

}