.amoa {
  padding-top: 120px;
  background-color: #fff;

  /* .amoa__title */
  &__title {
    color: #006993;
    text-transform: uppercase;
    font-size: 34px;
    margin-bottom: .5rem;
    font-weight: 500;
    line-height: 1.2;
    text-align: center;
  }

  /* .amoa__subtitle */
  &__subtitle {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: .5rem;
    font-weight: 500;
    line-height: 1.2;
    font-size: 24px;
    color: #427388;
    text-transform: uppercase;
    padding-left: 30px;


  }

  /* .amoa__text */
  &__text {
    margin-top: 7%;
    font-size: 18px;
    margin-bottom: 1rem;
    color: #212529;
  }

  /* .amoa__list */
  &__list {
    max-width: 920px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
  }


  /* .amoa__item */
  &__item {
    align-self: start;
    list-style-type: disc;
    line-height: 1.5;
    margin-left: 0;
    font-size: 15px;
    color: rgb(33, 37, 41);
  }

  /* .amoa__item-title */
  &__item-title.amoa__item {
    margin-top: 7%;
    font-size: 17px;
    color: #427388;
    margin-bottom: 1rem;
    text-align: center;
    align-self: center;
    list-style-type: none;
    line-height: 1.5;


  }

  // .amoa-preferable__item
  &-preferable__item {


    &_1 {
      position: relative;
      list-style-type: none;


    }

    &_2 {
      position: relative;
      list-style-type: none;


    }

    &_3 {
      position: relative;
      list-style-type: none;


    }
  }
}





.spontaneous-form {
  padding: 60px 0;
  background-color: #fff;

  & .form {
    background-color: #fff;
    max-width: 1130px;
    margin: 0 auto;

    /* .form__title */
    &__title {
      text-transform: uppercase;
      color: #006993;
      font-size: 28px;
      font-weight: 500;
      text-align: center;
    }

    /* .form__inner */
    &__inner {}

    /* .form__list */
    &__list {
      margin-top: 60px;
    }

    /* .form__item */
    &__item {
      list-style-position: outside;
      list-style: inside;
      font-family: montserrat-medium;
      font-size: 15px;
      margin-left: 0;
      line-height: 1.5;
      text-align: left;
      list-style-type: disc;
      text-align: center;
    }

    // .form__item-title
    &__item-title.form__item {
      font-size: 18px;
      color: rgb(33, 37, 41);
      list-style-type: none;
      margin-top: 6%;
      margin-bottom: 1rem;
      text-align: center;
    }
  }

  & .form-input-block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1);
    grid-column-gap: rem-calc(20);
    grid-row-gap: rem-calc(20);
  }

  & .form-label {

    /* .form-label__title */
    &__title {
      font-size: 18px;
      color: rgb(0, 105, 147);
      line-height: 1.6;
    }

    /* .form-label__input */
    /* .form-label__input */

    &__input {
      display: block;
      width: 100%;

    }

    & .form-input {
      box-sizing: border-box;
      width: 100%;
      padding: 1em;
      border: 1px solid #c9e6ff;
      box-sizing: border-box;

      &:focus {
        outline: 1px solid rgb(229, 151, 0);
        border: none;
      }

      &:active {
        outline: 1px solid rgb(229, 151, 0);
        border: none;
      }
    }

    /* .form-label__textarea */
    &__textarea {}
  }

  & .form-name {

    /* .form-name__title */
    &__title {}

    /* .form-name__input */
    &__input {}
  }

  & .form-input {
    border: none;
    box-sizing: border-box;
    width: 100%;
  }

  & .form-email {

    /* .form-email__title */
    &__title {}

    /* .form-email__input */
    &__input {}
  }

  & .form-label-textarea {
    display: block;
    margin-top: 20px;
  }

  & .form-textarea {

    /* .form-textarea__title */
    &__title {}

    /* .form-textarea__textarea */
    &__textarea {
      & textarea {
        display: block;
        width: 100%;
        border: 1px solid #c9e6ff;
        box-sizing: border-box;

        &:focus {
          outline: 1px solid rgb(229, 151, 0);
          border: none;
        }

        &:active {
          outline: 1px solid rgb(229, 151, 0);
          border: none;
        }
      }

    }
  }

  & .form-btn {
    display: flex;
    margin-top: 30px;

    /* .form-btn__attachmants */
    &__attachmants {
      display: block;
      width: 49%;
      box-sizing: border-box;
      margin-right: 2%;
      border-radius: 4px;
    }

    /* .form-btn__postulate */
    &__postulate {
      display: block;
      width: 49%;
      box-sizing: border-box;
      border-radius: 4px;

    }
  }

  & .btn {

    /* .btn__body-wrapp */
    &__body-wrapp {}

    /* .btn__body */
    &__body {}

    /* .btn__text */
    &__text {}
  }
}

.btn-attachmants {}

.btn-form {
  background: #c9e6ff;
  border: 0;
  width: 100%;
  padding: 1em;
  webkit-tap-highlight-color: transparent;
  display: inline-block;
  white-space: nowrap;
  text-decoration: none;
  vertical-align: baseline;
  text-align: center;
  margin: 0;
  min-width: 64px;
  line-height: 36px;
  border-radius: 4px;
  overflow: visible;
  transform: translate3d(0, 0, 0);
  transition: background .4s cubic-bezier(.25, .8, .25, 1), box-shadow 280ms cubic-bezier(.4, 0, .2, 1);
  color: #007bff;
  text-transform: uppercase;
  box-sizing: border-box;
  border-radius: 4px;

  &:hover {
    background: #92bde7;
    color: #fff;
    outline: 0;
    transition: background-color 2s ease-out;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2), 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12);
  }
}

