  /* .btn-form__add */
  cursor: pointer;
  transition: all .25s;

  &__add {}

  /* .btn-form__cancel */
  &__cancel {
    background-color: transparent;
    color: rgb(33, 37, 41);
    border-radius: 4px;
    padding: 0 16px;
    text-align: center;
    box-shadow: none;
    box-sizing: border-box;
    position: relative;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
    outline: 0;
    border: none;
    -webkit-tap-highlight-color: transparent;
    display: inline-block;
    white-space: nowrap;
    text-decoration: none;
    vertical-align: baseline;
    text-align: center;
    margin: 0;
    min-width: 64px;
    line-height: 36px;
    padding: 0 16px;
    border-radius: 4px;
    overflow: visible;
    transform: translate3d(0, 0, 0);
    transition: background .4s cubic-bezier(.25, .8, .25, 1), box-shadow 280ms cubic-bezier(.4, 0, .2, 1);
    font-size: 14px;
    font-weight: 500;
    float: right;
    border: none;

  }

