}

.modal-form--focus {
  &:focus {
    outline: 1px solid rgb(229, 151, 0);
    border: none;
  }

  &:active {
    outline: 1px solid rgb(229, 151, 0);
    border: none;
  }
}

@media (max-width: 767px) {
  .spontaneous-form {
    .form__title {
      font-size: 18px;
    }

    .form-label__title {
      font-size: 14px;
    }

    .form-input-block {
      grid-template-columns: repeat(1, 1fr);
    }

    .form-btn {
      flex-wrap: wrap;
    }

    .form-btn__attachmants {
      width: 100%;
      margin-bottom: 20px;
      margin-right: 0;
    }

    .form-btn__postulate {
      width: 100%;
    }

    .form__item-title.form__item {
      font-size: 14px;
    }

    .form__item {
      font-size: 12px;
    }
  }

  .amoa__title {
    font-size: 22px;
  }

  .amoa__subtitle {
    font-size: 18px;
  }

  .amoa__text {
    font-size: 14px;
  }

  .amoa__item-title.amoa__item {
    font-size: 14px;
  }

  .amoa__item {
    font-size: 12px;
  }
}







.modal {
  position: fixed;
  display: flex;
  display: none;
  justify-content: center;
  align-items: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background-color: rgba(256, 256, 256, 0.4);
  //   background-color: rgba(0, 0, 0, 0.4);
  // .modal__form-wrapp

  &__form-wrapp {
    width: 100%;
    height: 100%;
  }

  // .modal__form

  &__form {
    position: relative;
  }
}

.modal-inner {
