.first-business-section {
    padding: 100px 0 0;
}

.first-business__body {
    display: flex;
    align-items: center;
}

.first-business__title {
    flex-basis: 50%;
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 35px;
    line-height: 123.5%;
    text-transform: uppercase;
    margin-right: 20px;

    color: #5CEFC1;
}

.business-table {
    display: flex;
    justify-content: center;
    padding-top: 50px;

    img {
        width: 100%;
    }
}

.infrastructure__item_hidden {
    visibility: hidden;
}

@media(max-width:550px) {

    .first-business__title {
        font-size: 20px;
        margin-bottom: 25px;
    }

    .first-business__body {
        flex-direction: column;
    }



    .infrastructure__item_hidden {
        display: none;
    }
}

//analysis

.section-business-analysis {
    padding: 80px 0;
}

.business-analysis__body {
    display: flex;
    margin-top: 60px;
}

.business-analysis__col:first-child {
    margin-right: 60px;
}

.business-analysis__title {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 35px;
    line-height: 123.5%;
    text-transform: uppercase;
    color: #5CEFC1;
    max-width: 550px;
}

.business-analysis__h3 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
    line-height: 147%;
    text-transform: uppercase;
    color: #00516B;
    max-width: 330px;
}

.business-analysis__list {
    margin-top: 30px;
    list-style: none;
    font-size: 17px;

    li {
        display: flex;
        align-items: center;
    }

    li:not(:first-child) {
        margin-top: 15px;
    }
}

.business-analysis__item:last-child {
    margin-top: 60px;
}

@media(max-width:767px) {
    .business-analysis__col:first-child {
        margin-right: 0;
    }

    .business-analysis__body {
        flex-direction: column;
    }

    .business-analysis__col:last-child {
        margin-top: 50px;
    }

    .business-analysis__title {
        font-size: 20px;
    }

    .business-analysis__h3 {
        font-size: 14px;
    }

    .business-analysis__body {
        margin-top: 30px;
    }

    .business-analysis__list {
        margin-top: 15px;
    }

    .business-analysis__item:last-child {
        margin-top: 30px;
    }
}

@media(max-width:550px) {
    .business-analysis__list {
        font-size: 14px;
    }
}