.section-data-brain {
    position: relative;
    background-image: url(../img/svg/bg/data-sec-bg-1.png);
    background-color: linear-gradient(94.46deg, #36EAC2 3.97%, #31E9C5 25.79%, #07E0DE 51.58%, #46EDB9 76.54%);
    background-size: cover;
    background-position: 50% 50%;
    padding: 80px 0;
}

.data-brain__body {
    position: relative;
    z-index: 10;
    display: flex;
    align-items: center;
}

.data-brain__item {
    flex-basis: 50%;
}

.data-brain__item:last-child {
    margin-left: 50px;
}

.data-brain__p:not(:first-child) {
    margin-top: 20px;
}

@media(max-width:550px) {
    .data-brain__body {
        flex-direction: column;
    }

    .data-brain__item:last-child {
        margin-left: 0;
        margin-top: 40px;
    }
}


//2


.section-tech-descr {
    padding: 100px 0;
}

.tech-descr__body {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
}

.tech-descr__item {
    flex-basis: calc(33.333% - 30px);

    img {
        display: block;
        margin-top: -50px;
        margin-left: auto;
        margin-right: auto;
    }
}

.tech-descr__list {
    list-style: none;

    li {
        display: flex;
        align-items: center;
    }

    li:not(:first-child) {
        margin-top: 15px;
    }
}

.tech-descr__h2 {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 123.5%;
    /* or 31px */

    text-transform: uppercase;

    color: #00516B;
}

.tech-descr__h3 {
    margin-top: 40px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
    line-height: 147%;
    /* or 25px */

    text-transform: uppercase;

    color: #00516B;
}



@media(max-width:650px) {



    .section-infrastructure_data {
        padding-top: 100px !important;
        padding-top: 0;
    }

    .tech-descr__item {
        flex-basis: calc(50% - 15px);
    }

    .tech-descr__item:last-child {
        display: none;
    }

    .section-tech-descr {
        padding: 70px 0;
    }

    .tech-descr__h3 {
        margin-top: 30px;
    }
}



//take-care



.take-care__body {
    display: flex;
    justify-content: space-between;
}

.take-care__item {
    list-style: none;

    li {
        display: flex;
        align-items: center;
    }

    li:not(:first-child) {
        margin-top: 15px;
    }
}

.take-care__title {
    margin-bottom: 30px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
    line-height: 147%;
    /* or 25px */

    text-transform: uppercase;

    color: #00516B;
}

@media(max-width:1024px) {
    .data-brain__item_take-care {
        flex-basis: 35%;
    }
}

@media(max-width:650px) {
    .data-brain__body_take-care {
        flex-direction: column;

        .data-brain__item:last-child {
            margin-top: 20px;
        }
    }
}






.section-tech-descr-lists {
    padding: 100px 0;
}

.tech-descr-lists__title {
    font-family: Montserrat;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 123.5%;
    /* or 31px */

    text-transform: uppercase;

    color: #00516B;
}


.tech-descr-lists__body {
    margin-top: 50px;

    display: flex;
    justify-content: space-between;
}

.tech-descr-lists__body_short {
    max-width: 80%;
}

.tech-descr-lists__item_fb20 {
    flex-basis: 15% !important;
}

.tech-descr-lists__item:not(:first-child) {
    margin-left: 15px;
}

.tech-descr-lists__item {
    flex-basis: calc(33.333% - 40px);
}

.tech-descr-list {
    display: flex;
}

.tech-descr-list:not(:first-child) {
    margin-top: 15px;
}

.tech-descr-lists__h3 {
    margin-bottom: 25px;

    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
    line-height: 147%;
    /* or 25px */

    text-transform: uppercase;

    color: #00516B;
}

.tech-descr-list__p {
    margin-top: -9px;
}


@media(max-width:768px) {
    .tech-descr-lists__item {
        flex-basis: calc(33.333% - 20px);
    }
}

@media(max-width:550px) {

    .tech-descr-lists__body {
        flex-direction: column;
    }

    .tech-descr-lists__item:not(:first-child) {
        margin-top: 30px;
        margin-left: 0;
    }




    .tech-descr-lists__h3 {
        font-size: 18px;
    }
}

.dflex_li {
    display: flex;

    span:last-child {
        margin-top: -8px;
    }
}

.soft-dev__p_dflex {
    p:not(:first-child) {
        margin-top: 20px;
    }
}