.section-our-presence {
    padding: 60px 0;
}

.our-presence__body {
    position: relative;
    z-index: 10;
    width: 100%;
    display: flex;
}

.our-presence__item:first-child {
    flex-basis: 30%;
}

.our-presence__item:last-child {
    flex-basis: 70%;

    img {
        width: 100%;
        object-fit: contain;
        object-position: 50% 50%;
    }
}

.our-presnce__title {
    text-transform: uppercase;
    font-size: 35px;

    font-family: "Montserrat", sans-serif;
}


.our-presnce-num {
    margin-top: 70px;
}

.our-presnce-num__item:not(:first-child) {
    margin-top: 20px;
}

.our-presnce-num__descr {
    display: block;
    font-weight: bold;
    font-size: 25px;
}

.our-presnce-num__num {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    font-size: 70px;
    line-height: 123.5%;
    text-align: center;
    text-transform: uppercase;
    color: #19E4D3;
}


@media(max-width:1024px) {
    .our-presence__body {
        align-items: center;
    }

    .our-presnce-num {
        margin-top: 40px;
    }
}

@media(max-width:767px) {
    .our-presence__body  {
        flex-direction: column;
        align-items: stretch;
    }

    .our-presnce-num  {
        width: 100%;
        display: flex;
        justify-content: space-around;
    }
    .our-presnce-num__item:not(:first-child) {
        margin-top: 0;
        margin-left: 10px;
    }

    .our-presence__item:last-child  {
        margin-top: 40px;
    }
}

@media(max-width:550px) {
    .our-presnce-num__num {
        font-size: 42px;
    }

    .our-presnce-num__descr {
        font-size: 14px;
    }


    .our-presnce-num {
        justify-content: space-between;
    }

    .section-our-presence {
        padding: 40px 0;
    }
    .our-presnce-num {
        margin-top: 20px;
    }
}