.section-our-commitment {
    background-image: url(../img/bg/our-commitment-bg.png);
    background-position: 50% 50%;
    background-size: cover;
    padding: 4rem 0;
}

.our-commitment {

    &__row {
        margin-top: 2rem;
        display: flex;
        justify-content: space-between;
    }

    &__title {
        position: relative;
        z-index: 10;
        text-transform: uppercase;
        text-align: center;
    }




}

.our-commmitment__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding: 50px 0;
    flex-basis: calc(25% - 25px);
    background-color: $main-color;
}

.commitment-card__icon {
    height: 80px;
    width: 80px;
    object-fit: contain;
}

.our-commmitment__link-wrap {
    margin-top: 30px;
    transition: all .3s;

    img {
        transition: all .3s;
    }

    &:hover {
        img {
            transform: scale(1.2);

        }
    }
}

.commitment-card__title {
    text-transform: uppercase;
    text-align: center;
    color: #fff;
    font-size: 22px;
    margin-top: 15px;
}

.commitment-card {
    position: relative;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    transition: all .35s;

    &__title {
        text-transform: uppercase;
        text-align: center;
        color: #fff;
        font-size: 22px;
    }

    &:hover+ {
        transition: all .35s;

        .commitment-hidden {
            position: relative;
            opacity: 1;
            z-index: 2;
            transform: translateY(-100%) scale(1);
        }
    }

}

.commitment-hidden {
    position: absolute;
    z-index: -1;
    background-color: #006993;
    color: #fff;
    transform: translateY(-100%) scale(0);
    opacity: 0;
    transition: all .35s;



    &:hover {
        position: relative;
        opacity: 1;
        z-index: 2;
        transition: all .35s;
        transform: translateY(-100%) scale(1);
        opacity: 1;
    }

    &__list {
        list-style: none;
        padding: 1.25rem 0.75rem;
    }
}


@media(max-width:768px) {
    .commitment-card {
        padding: 2.5rem 0;
    }

    .our-commitment {
        &__row {
            flex-direction: column;
        }
    }


    .our-commmitment__item:not(:first-child) {
        margin-top: 2rem;
    }


    .commitment-card {

        &:hover+ {
            transition: all .35s;

            .commitment-hidden {
                position: relative;
                opacity: 1;
                z-index: 2;
                transform: translateY(10px) scale(1);
            }
        }
    }

    .commitment-hidden {
        &:hover {
            position: relative;
            opacity: 1;
            z-index: 2;
            transition: all .35s;
            transform: translateY(10px) scale(1);
            opacity: 1;
        }
    }


}