.modal_1 {
    display: none;
    width: 70%;
    background-color: #eceaf5;
    border-radius: 25px;
    box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.5);

    &__header {
        font-weight: 700;
        height: 40px;
        font-size: 15px;
        text-align: left;
        color: #00516b;
        text-transform: uppercase;
        border-bottom: 1px solid grey;
    }

    &__title {
        padding-bottom: 20px;
    }

    &__content {
        padding: 20px;
    }

    &__img {
        display: flex;
        justify-content: center;
    }

    &__img:not(:first-child) {
        margin-top: 1.5rem;
    }


}

.modal_1-btn {
    margin-top: 2rem;
    display: block;
    width: 100%;
}

#modal__digitall-card_5 {
    width: 85%;
}

#modal__hint-card_3 {
    & .modal_1__content {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
    }

    & .modal_1__list {
        width: 50%;

    }

    & .modal_1__img {

        width: 50%;

    }

}

.modal__digitall {
    .modal_1__text {
        font-size: 20px;
        line-height: 1.2;

    }

    & .modal_1-btn {
        padding: .5rem 1rem;
        font-size: 1.25rem;
        line-height: 1.5;
        border-radius: .3rem;
        background-color: #007bff;
        border-color: #007bff;
        text-align: center;
        color: #fff;
    }

    & .modal_1-list {
        & .modal_1__item {
            list-style-type: none;

            & .modal_1-list__title {
                width: 100%;
                background-color: #46edb9;
                color: #fff;
                text-align: center;
                margin: 20px 0;

            }

            & li {
                margin-left: 30px;
                padding: 10px 0;
            }
        }

    }
}

#modal__hint-card_3 {
    .modal_1__block {
        display: flex;
        justify-content: center;
    }
}

#modal__hint-card_1 {
    .modal_1-btn {
        display: flex;
        justify-content: center;

        & a {
            display: block;
            width: 50%;
            color: #fff;
            background-color: #007bff;
            border-color: #007bff;
            text-align: center;
            display: inline-block;
            font-weight: 400;
            user-select: none;
            padding: .375rem .75rem;
            font-size: 1rem;
            line-height: 1.5;
            border-radius: .25rem;
            text-decoration: none;

            &:hover {
                color: #fff;
                background-color: #0069d9;
                border-color: #0062cc;
                width: 50%;
            }

            &:focus {
                color: #fff;
                background-color: #0069d9;
                border-color: #0062cc;
            }

            &:active {
                color: #fff;
                background-color: #0069d9;
                border-color: #0062cc;
            }
        }
    }
}




.digitall-table {
    display: flex;
    justify-content: space-between;

    &__col:first-child {
        flex-basis: 50%;
    }

    &__col:last-child {
        flex-basis: 40%;
    }

    &__h3 {
        text-align: center;
        margin-bottom: 1.5rem;
    }

    &__h4 {
        text-align: center;
    }

    &__list {
        margin: 1rem 0;
        border-top: 1px solid #ccc;
        padding-top: 1rem;
        list-style: none;
    }
}

.popupSlider {
    background-color: #fff;
    box-shadow: 0 0 8px 1px rgba(204, 204, 204, 0.74);
    border-radius: 25px;
}

@media(max-width:768px) {
    .digitall-table {
        flex-direction: column;
    }
}

@media(max-width:550px) {
    .digitall-table {
        font-size: 0.9rem;
    }


    #modal__hint-card_3 {
        & .modal_1__block {
            display: flex;
            flex-direction: column;
        }

        & .modal_1__list {
            width: 100%;

        }

        & .modal_1__img {

            width: 100%;

        }

    }
}





.borderspacing {
    & th {
        padding: 15px;
        font-size: 10px;
        border: 1px solid #fff;
        text-align: center;
    }

    td {
        padding: 15px;
        font-size: 14px;
        background-color: #f1fbff;
        width: 25%;
        font-size: 14px;
        border: 1px solid #fff;
        text-align: center;

        & li {
            text-align: left;
            display: list-item;
        }
    }

    & thead {
        width: 100%;

        & th {
            background-color: #1828bb;
            text-align: center;
            color: #fff;


        }
    }

    & tbody {
        & th {
            background-color: rgb(24, 116, 221);
            color: #fff;
        }
    }
}

.borderspacing_1 {
    width: 100%;
    font-size: 16px;
    text-transform: uppercase;

    & thead {
        color: rgb(26, 108, 231);
        font-weight: bold;

        & th {
            text-align: center;
            list-style-type: none;
        }
    }

    & .th-title {
        color: #00516b;
        text-align: center;
        list-style-type: none;
    }
}

@media (max-width: 991px) {
    .borderspacing {
        & th {
            padding: 12px;
            font-size: 8px;

        }

        td {
            padding: 12px;
            font-size: 10px;




        }
    }
}

.modal_1-slider {
    & li {
        list-style-type: none;
    }
}




@media (max-width: 767px) {
    .borderspacing {
        & th {
            padding: 3px;
            font-size: 7px;

        }

        td {
            padding: 3px;
            font-size: 8px;





        }
    }

    #modal__hint-card_1-table {
        width: 100%;

        & .modal_1__content {
            padding: 0;
            margin: 0
        }

        & .modal_1__header {
            border: none;
        }

    }
}


.modal_1-btn a {
    text-decoration: none;
    color: #fff;
}






#modal__digitall-card_1-details {
    background-color: #fff;

    & .modal_1__title {
        text-align: center;
        font-size: 32px;
        text-transform: uppercase;
        color: #00516b;
        line-height: 1.2;
        font-weight: 500;
    }

    & .modal_1__item {
        display: flex;
        justify-content: space-between;
    }

    & .modal_1__item-title {
        color: rgb(40, 121, 202);
        font-size: 24px;
    }
}

@media (max-width: 991px) {
    .borderspacing {
        & th {
            padding: 12px;
            font-size: 8px;

        }

        td {
            padding: 12px;
            font-size: 10px;




        }
    }

    #modal__digitall-card_1-details {
        & .modal_1__title {

            font-size: 22px;


        }

        & .modal_1__item {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 40px;
        }

        & .modal_1__item-title {
            color: rgb(40, 121, 202);
            font-size: 16px;
            text-align: center;
        }
    }
}

.modal_1-slider {
    & li {
        list-style-type: none;
    }
}




@media (max-width: 767px) {
    .borderspacing {
        & th {
            padding: 3px;
            font-size: 7px;

        }

        td {
            padding: 3px;
            font-size: 8px;





        }
    }

    #modal__hint-card_1-table {
        width: 98%;

        & .modal_1__content {
            padding: 0;
            margin: 0 -40px;
        }

        & .modal_1__header {
            border: none;
        }

    }

    #modal__digitall-card_1-details {
        & .modal_1__title {

            font-size: 16px;

        }

        & .modal_1__item {

            margin-bottom: 20px;
        }

        & .modal_1__item-title {

            font-size: 14px;

        }
    }
}