.infrastructure-animation {
    position: absolute;

    &_1 {
        max-width: 30%;
        top: 0;
        left: 0;
    }

    &_2 {
        max-width: 55%;
        top: 0;
        right: 12%;
    }

    &_3 {
        bottom: -200px;
        left: 30%;
    }


    &_4 {
        right: 0;
        bottom: -30%;
        max-width: 35%;

    }

    &_5 {
        top: 120px;
        left: -150px;
        max-width: 300px;
    }

    &_6 {
        bottom: 20px;
        right: 5%;
        max-width: 300px;
    }

    &_7 {
        bottom: 20px;
        right: 5%;
        max-width: 300px;
    }

    &_8 {
        bottom: 20px;
        right: -20px;
        max-width: 300px;
    }

    &_10 {
        left: 0;
        top: 0;
    }

    &_11 {
        right: 0;
        bottom: 0;
    }


}


@media(max-width:1240px) {
    .infrastructure-animation {
        position: absolute;

        &_3 {
            bottom: -200px;
            left: 5%;
        }

        &_2 {

            right: 5%;
        }

        &_5 {
            display: none;
        }
    }



    .infrastructure-animation_3-data {
        max-width: 120px;
        bottom: -50px;
      
    }
}

.infrastructure-animation_3-data {
    max-width: 200px;
    bottom: -100px;

}

.infrastructure-animation_4-data {
    right: -50px;
    bottom: -100px;
}


@media(max-width:768px) {
    .infrastructure-animation_7 {
        display: none;
    }
}

@media(max-width:550px) {
    .infrastructure-animation {
        position: absolute;

        &_1 {
            display: none;
        }

        &_2 {
            display: none;
        }

        &_3 {
            display: none;
        }

        &_4 {
            bottom: -20%;
            max-width: 300px;
            right: 5%;
        }

        &_6 {
            display: none;
        }
    }

    .infrastructure-animation_2-devops {
        display: block;
        top: 0;
        right: -80px;
    }


    .infrastructure-animation_3-devops {
        display: block;
        left: -140px;
        max-width: 240px;
        bottom: 25%;
    }

    .infrastructure-animation_4-devops {
        right: -80px;
    }



    .infrastructure-animation_2-data {
        display: block !important;
        max-width: 120px;
        right: auto !important;
        left: 0;
        top: 80px;
    }

    .infrastructure-animation_3-data {
        display: block !important;
        max-width: 120px;
        left: auto !important;
        right: 0;
        top: 80px;
    }

    .infrastructure-animation_4-data {
        max-width: 260px !important;
        right: -50px !important;
    }


}